//General document settings and info
class Doc extends React.Component {
  //Set title of document - for bookmarking
  componentDidMount() {
    document.title = "Jonathan Rintala";
  }

  //Get width of device
  getWidthOfDevice() {
    return (
      <div>
        <meta name="viewport" content="width=device-width, initial-scale=1" />
      </div>
    );
  }
  render() {
    return this.getWidthOfDevice();
  }
}

